import React from "react";
import {BrowserRouter as Router, Route} from "react-router-dom";
import App from "../App";
import CreativeEditorPage from "../pages/creative-editor/creative-editor.page";
import HomePage from "../pages/home/home.page";

const AppRouter = () => {
    return (
        <Router basename="motifin">
            <div>
                <Route exact path={"/"} component={CreativeEditorPage}/>
                <Route path={"/home"} component={CreativeEditorPage}/>
                <Route path={"/creative-editor"} component={CreativeEditorPage}/>
            </div>
        </Router>
    )
};

export default AppRouter
